import React from 'react';

interface BusinessSectorSelectProps {
  value?: string;
  onChange?: (value: string) => void;
}

const businessSectors = [
  { value: "", label: "... اختر الصناعة" },
  { value: "technology", label: "التكنولوجيا والبرمجيات" },
  { value: "retail", label: "التجارة والبيع بالتجزئة" },
  { value: "healthcare", label: "الرعاية الصحية" },
  { value: "education", label: "التعليم والتدريب" },
  { value: "finance", label: "الخدمات المالية والمصرفية" },
  { value: "food", label: "الأغذية والمشروبات" },
  { value: "construction", label: "البناء والتشييد" },
  { value: "manufacturing", label: "التصنيع والإنتاج" },
  { value: "transportation", label: "النقل واللوجستيات" },
  { value: "real-estate", label: "العقارات" },
  { value: "consulting", label: "الاستشارات" },
  { value: "marketing", label: "التسويق والإعلان" },
  { value: "entertainment", label: "الترفيه والإعلام" },
  { value: "agriculture", label: "الزراعة" },
  { value: "tourism", label: "السياحة والسفر" },
  { value: "other", label: "أخرى" }
];

export default function BusinessSectorSelect({ value, onChange }: BusinessSectorSelectProps) {
  return (
    <div className="mb-5">
      <label htmlFor="businessSector" className="block mb-2 font-bold text-gray-700">
        قطاع العمل
      </label>
      <select
        id="businessSector"
        value={value}
        onChange={(e) => onChange?.(e.target.value)}
        className="w-full p-3 border border-gray-300 rounded-md text-base bg-white focus:outline-none focus:ring-2 focus:ring-blue-500"
      >
        {businessSectors.map((sector) => (
          <option key={sector.value} value={sector.value}>
            {sector.label}
          </option>
        ))}
      </select>
    </div>
  );
}
