import React from 'react';

interface BusinessSizeSelectProps {
  value?: string;
  onChange?: (value: string) => void;
}

const businessSizes = [
  { value: "micro", label: "صغير جداً (1 موظف)" },
  { value: "small", label: "صغير (2 - 10 موظفين)" },
  { value: "medium", label: "متوسط (11 - 50 موظف)" },
  { value: "large", label: "كبير (51 - 250 موظف)" },
  { value: "enterprise", label: "مؤسسة كبيرة (أكثر من 250 موظف)" }
];

export default function BusinessSizeSelect({ value, onChange }: BusinessSizeSelectProps) {
  return (
    <div className="mb-8">
      <label htmlFor="businessSize" className="block mb-2 font-bold text-gray-700">
        حجم العمل
      </label>
      <select
        id="businessSize"
        value={value}
        onChange={(e) => onChange?.(e.target.value)}
        className="w-full p-3 border border-gray-300 rounded-md text-base bg-white focus:outline-none focus:ring-2 focus:ring-blue-500"
      >
        {businessSizes.map((size) => (
          <option key={size.value} value={size.value}>
            {size.label}
          </option>
        ))}
      </select>
    </div>
  );
}
