import React from 'react';

interface SubmitButtonProps {
  onClick?: () => void;
  disabled?: boolean;
  loading?: boolean;
  text?: string;
}

export default function SubmitButton({
  onClick,
  disabled = false,
  loading = false,
  text = "التالي"
}: SubmitButtonProps) {
  return (
    <button
      type="submit"
      onClick={onClick}
      disabled={disabled || loading}
      className={`w-full p-4 font-bold text-lg rounded-md cursor-pointer transition-colors duration-300 ${
        disabled || loading
          ? 'bg-gray-400 text-gray-600 cursor-not-allowed'
          : 'bg-blue-600 text-white hover:bg-blue-700'
      }`}
    >
      {loading ? 'جاري التحميل...' : text}
    </button>
  );
}
