import React from 'react';

interface BusinessNameInputProps {
  value?: string;
  onChange?: (value: string) => void;
  placeholder?: string;
}

export default function BusinessNameInput({
  value,
  onChange,
  placeholder = "... اسم العمل"
}: BusinessNameInputProps) {
  return (
    <div className="mb-5">
      <label htmlFor="businessName" className="block mb-2 font-bold text-gray-700">
        اسم العمل
      </label>
      <input
        type="text"
        id="businessName"
        value={value}
        onChange={(e) => onChange?.(e.target.value)}
        placeholder={placeholder}
        className="w-full p-3 border border-gray-300 rounded-md text-base focus:outline-none focus:ring-2 focus:ring-blue-500"
      />
    </div>
  );
}
