import React from 'react';

interface WebsiteUrlInputProps {
  value?: string;
  onChange?: (value: string) => void;
  placeholder?: string;
}

export default function WebsiteUrlInput({
  value,
  onChange,
  placeholder = "www.yourwebsite.com"
}: WebsiteUrlInputProps) {
  return (
    <div className="mb-5">
      <label htmlFor="websiteUrl" className="block mb-2 font-bold text-gray-700">
        عنوان موقع النشاط التجاري
      </label>
      <div className="flex items-center">
        <span className="p-3 border border-gray-300 border-r-0 rounded-l-md bg-gray-200 text-gray-600">
          https://
        </span>
        <input
          type="text"
          id="websiteUrl"
          value={value}
          onChange={(e ) => onChange?.(e.target.value)}
          placeholder={placeholder}
          className="flex-grow p-3 border border-gray-300 rounded-r-md text-base focus:outline-none focus:ring-2 focus:ring-blue-500"
        />
      </div>
    </div>
  );
}
