import React from 'react';

interface BusinessFormContainerProps {
  children: React.ReactNode;
}

export default function BusinessFormContainer({ children }: BusinessFormContainerProps) {
  return (
    <div className="flex justify-center items-start min-h-screen bg-gray-100 p-5">
      <div className="bg-white p-10 rounded-lg shadow-md w-full max-w-xl">
        <h1 className="text-2xl font-bold mb-2 text-gray-800">دعنا نُعد إعداد عملك الأول</h1>
        <p className="text-base text-gray-600 mb-8">أخبرنا المزيد عن عملك</p>
        
        {children}
      </div>
    </div>
  );
}
